/*******************************************************************************
 * Variables used throughout the theme.
 * To adjust anything, simply edit the variables below and rebuild the theme.
 ******************************************************************************/


// Academic Color Palette
$red-color: #d32f2f !default; // More muted red
$red-color-dark: #b71c1c !default;
$orange-color: #f57c00 !default; // More muted orange
$blue-color: #1976d2 !default; // Academic blue
$blue-color-dark: #0d47a1 !default;
$cyan-color: #00695c !default; // Teal for academic feel
$light-cyan-color: lighten($cyan-color, 25%);
$green-color: #388e3c !default; // More muted green
$green-color-lime: #689f38 !default;
$green-color-dark: #2e7d32 !default;
$green-color-light: #e8f5e8 !default;
$green-color-bright: #4caf50 !default;
$purple-color: #512da8 !default; // More muted purple
$light-purple-color: lighten($purple-color, 25%);
$pink-color: #c2185b !default; // More muted pink
$pink-color-light: #fce4ec !default;
$yellow-color: #fbc02d !default; // More muted yellow

// Academic Grays
$grey-color: #616161 !default; // More neutral gray
$grey-color-light: lighten($grey-color, 35%);
$grey-color-dark: #212121 !default; // Darker, more professional
$grey-900: #1a1a1a !default;

$white-color: #ffffff !default;
$black-color: #1a1a1a !default; // Slightly softer black


// Theme colors

$code-bg-color-light: rgba($purple-color, 0.05);
$code-bg-color-dark: #2c3237 !default;
